import HttpClient from './b8one.httpClient';

export default class TransporterService {
  constructor() {
    this.httpClient = new HttpClient();
  }

  /**
   * This function return all active hubs from HUB Entity.
   * @param  {Array|String} fields=['id', 'initialZipcode', 'finalZipcode']
   */
  async getALLHUBS(fields = ['id', 'initialZipcode', 'finalZipcode']) {
    try {
      const endpoint = `/api/dataentities/HB/search?_fields=${fields}&_where=active=true`;

      return this.httpClient.get(endpoint);
    } catch (error) {
      console.error(error);
    }
  }

  /**
   * This functions get and filter the transporters by hubID property.
   * @param  {String} hubID The documentID of a document from HUB Entity.
   * @param  {Array|String} fields Document fields what you need
   */
  async getTransportersByHUB(hubID, fields) {
    try {
      const query = `(status=true AND idHUB=${hubID})`;

      const endpoint = `/api/dataentities/TR/search?_fields=${fields}&_where=${query}`;

      return this.httpClient.get(endpoint);
    } catch (error) {
      console.error(error);
    }
  }
}
