export default class CustomSelect {
  constructor({
    selectElement,
    customSelectList,
    customSelectSelected,
    customSelectListItemClassName,
    customSelectListItemClassNameActive,
    selectChangeCallback,
    selectOpenCallback,
    selectCloseCallback,
  }) {
    this.isOpen = false;

    this.selectElement =
      selectElement instanceof Element
        ? selectElement
        : document.querySelector(selectElement);

    this.customSelectList =
      customSelectList instanceof Element
        ? customSelectList
        : document.querySelector(customSelectList);

    this.customSelectSelected =
      customSelectSelected instanceof Element
        ? customSelectSelected
        : document.querySelector(customSelectSelected);

    this.selectChangeCallback = selectChangeCallback || new Function();
    this.selectOpenCallback = selectOpenCallback || new Function();
    this.selectCloseCallback = selectCloseCallback || new Function();
    this.customSelectListItemClassName = customSelectListItemClassName || '';
    this.customSelectListItemClassNameActive =
      customSelectListItemClassNameActive || '';

    this.setCurrentSelectedItem();
    this.createSelectList();
    this.setListenerForCurrentSelected();
  }

  setCurrentSelectedItem() {
    let { options, selectedIndex } = this.selectElement;

    this.customSelectSelected.textContent = options[selectedIndex].textContent;
  }

  handleSelectChange(target) {
    let { value } = this.selectElement;
    let { value: newValue } = target.dataset;

    const prevListItemActive = this.customSelectList.querySelector(
      `.${this.customSelectListItemClassNameActive}`
    );

    if (prevListItemActive) {
      prevListItemActive.classList.remove(
        this.customSelectListItemClassNameActive
      );
    }

    target.classList.add(this.customSelectListItemClassNameActive);

    this.close();

    if (value !== newValue) {
      this.changeOption(newValue);
    }
  }

  createSelectList() {
    [...this.selectElement].forEach(option => {
      const listItem = document.createElement('li');
      listItem.setAttribute('class', this.customSelectListItemClassName);
      listItem.dataset['value'] = option.value;

      listItem.textContent = option.textContent.trim();

      listItem.addEventListener('click', ({ target }) =>
        this.handleSelectChange(target)
      );

      this.customSelectList.append(listItem);
    });
  }

  setListenerForCurrentSelected() {
    this.customSelectSelected.addEventListener('click', () => {
      this.open();

      document.body.addEventListener('click', ({ target }) => {
        let close = !target.className.includes('custom-select');

        if (close) this.close();
      });
    });
  }

  open(event = null) {
    this.isOpen = true;
    this.selectOpenCallback(event);
    this.customSelectList.classList.add('open');
  }

  close() {
    this.isOpen = false;
    this.selectCloseCallback();
    this.customSelectList.classList.remove('open');
  }

  changeOption(value) {
    this.selectElement.value = value;

    const eventChange = new Event('change', { bubbles: true });

    this.selectElement.dispatchEvent(eventChange);

    this.setCurrentSelectedItem();
    this.selectChangeCallback({
      value,
    });
  }
}
